.page {
    width: 100%;
    height: 100%;
    position: absolute;
}

.top-tags {
  bottom: auto;
  top: 35px

}

.tags{
    color: #ffd700;
    opacity: 0.6;
    position: absolute;
    bottom: 0;
    left: 120px;
    font-size: 18px;
    font-family: 'La Belle Aurore';
}

.bottom-tag-html{
    margin-left: -20px;

}

.container {
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;

}

.about-page{

    overflow: auto;
    scrollbar-width: none;
    scrollbar-color: yellow black;


    .text-zone{
        position: absolute;
        left: 10%;
        top: 50%;
        width: 35%;
        vertical-align: middle;
        display: table-cell;
        max-height: 90%;
        transform: translateY(-50%);
    }

    h2 {
        font-size: 32px;
        font-family: 'Coolvetica';
        color: #ffd700;
        margin-top: 100px;

    }

    h1 {
        font-size: 53px;
        font-family: 'Coolvetica';
        color: #ffd700;
        font-weight: 400;
        margin-top: 0;
        position: relative;
        margin-bottom: 40;
        left: 10px;

        // &::before{
