        // }

    }

    p {
        font-style: 13px;
        color: #fff;
        font-family: sans-serif;
        font-size: 20px;
        font-weight: 210;
        min-width: fit-content;
        animation: pulse 1s;


        &:nth-of-type(1){
            animation-delay: 1.1s;
        }

        &:nth-of-type(2){
